@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Shadows+Into+Light&display=swap);
.Logo {
    background-color: #f5ba13;
    padding: 8px;
    box-sizing: border-box;
    border-radius: 5px;
    display: flex;
    text-align: center;
}

.Logo img {
    height: 100px !important;
    width: "10px" !important;
    margin: auto;
}

.Logo h1 {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}



*  {
     box-sizing: border-box;
     margin: 0;
     padding: 0;
    font-family:"Shadows Into Light","Indie Flower" ;
}

.header {
    width: 100%;
    background-color: #f5ba13;
    display: flex;
    justify-content: start;
    align-items: center;
    padding-left: 50px;
    color: aliceblue;
    box-shadow: 5px 3px 15px -5px rgba(0,0,0,0);
}

.footer {
    width: 100%;
    position: absolute;
    bottom: 0;
    text-align: center;
}

.main_note {
    width: 500px;
    padding: 15px 10px 5px 10px;
    margin: 20px auto;
}

form {
    color: #ecf0f1;
    padding: 15px 10px 5px 10px;
    min-height: 50px;
    border-radius: 10px;
    position: relative;
    box-shadow: 3px 5px 15px -5px rgba(0, 0, 0, 0.3); 
}

input {
    width: 100%;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1.5rem;
    margin: 10px 0;
    font-weight: bold;
}

textarea {
    width: 100%;
    height: 6rem;
    line-height: 30px;
    background: transparent;
    border: none;
    outline: none;
    font-size: 1rem;
    margin: 10px 0;
}


.MuiButton-root {
    width: 30px;
    height: 50px;
    position: absolute;
    bottom: 25px;
    left: 400px;
    cursor: pointer;
    background-color: #fff !important;
    box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
    border-radius: 50% !important;
  }

.MuiButton-root .plus_sign {
    font-size: 45px !important;
    font-weight: 900;
    color: #f4b400;
    cursor: pointer;
    z-index: 222;
    background: transparent;
  }


.MuiButton-root:hover {
    background: #f4b400 !important;
  }
  
  .MuiButton-root:hover .plus_sign {
    color: #fff;
    background: transparent;
  }

  
.font-style {
    font-family: 'Shadows Into Light', sans-serif;
  }

  .Logo h1 {
      margin: auto;
  }


  .note {
    background: #fff;
    border-radius: 7px;
    box-shadow: 0 2px 5px #ccc;
    padding: 10px;
    width: 240px;
    margin: 16px;
    float: left;
  }
  
  .note h1 {
    font-size: 1.1em;
    margin-bottom: 6px;
    font-family: 'Indie Flower', sans-serif;
  }
  
  .note p {
    font-size: 1.1em;
    margin-bottom: 10px;
    white-space: pre-wrap;
    word-wrap: break-word;
    font-family: 'Raleway', sans-serif;
  }
  
  .note .btn {
    position: relative;
    float: right;
    margin-right: 10px;
    color: #f4b400;
    border: none;
    width: 50px;
    height: 50px;
    background: transparent;
    cursor: pointer;
    outline: none;
    box-shadow: 3px 3px 15px -10px rgba(0, 0, 0, 0.3);
    border-radius: 50%;
  }
  
  .deleteIcon {
    width: 0.7rem;
    height: 0.7rem;
  }
  
  .note .btn:hover {
    background-color: #f5ba13;
  }
  
  .note .btn:hover .deleteIcon {
    color: #fff;
  }

 

 @media (max-width: 550px) {
    .main_note {
        padding: 5%;
        margin: auto 10px auto 10px;
        width:100%;
    }

    .MuiButton-root{
        left: 15px;
    }
} 






